import React from 'react';
import Rodape from '../../components/Rodape';
import Navegador from '../../components/NavBar';
import background from '../../assets/imgs/capa1.png';
import BlogPage1 from '../../components/BlogPage1';
import './styles.scss';

const Blog = () => {

  


  const texto = `A Secretaria de Sade do Amazonas (SES-Am) confirmou, na tarde desta quinta-feira (6), dois casos de infeco por Influenza A (H3N2) e Covid-19 no estado.

  De acordo com a SES, o primeiro caso foi diagnosticado em uma mulher, de 25 anos, que apresenta sintomas leves, como tosse. Ela  moradora de Manacapuru, Regio Metropolitana de Manaus, e retornou de So Paulo no dia 3.
  
  O segundo caso tambm foi diagnosticado em uma mulher. A paciente, de 24 anos, apresenta sintomas leves. Ela teve amostra coletada no dia 3 de janeiro, quando retornou de So Paulo.
  
  A Secretaria de Sade informou que as duas pacientes esto com o esquema vacinal completo, com as duas doses do imunizante contra a Covid-19.
  
  A chamada dupla contaminao  constatada quando os dois testes  para gripe e para Covid  do positivo. Ela foi apelidada de "flurona" (unio dos termos "flu", de influenza, com "rona" de coronavrus) na imprensa internacional, mas o termo no designa um novo tipo de doena, apenas uma forma simplificada de se referir  ocorrncia simultnea de contaminaes.`;

  
  return (
    <div >
      <div style={{
        backgroundImage: `url(${background})`,
        backgroundSize: "cover",


      }} >
       
        <Navegador />

        <div style={{ height: 5, backgroundColor: '#808080' }}>
        </div>
      </div>



      <div>
        <BlogPage1
          titulo="Brasil bate recorde de exportao de soja em 2021; milho cai ao menor patamar desde 2012"
          subtitulo="Pas vendeu 86,63 milhes de toneladas de soja ao exterior, no ano passado. Recorde anterior havia sido em 2018."
          data="05/01/2022"
          hora="10h52"
          redator="Aislan Silva Costa"
          texto1={texto}
        />

        <BlogPage1
          titulo="'King's Man: A origem' retrata absurdos 'historicamente corretos' da 1 Guerra Mundial"
          subtitulo="' inacreditvel que basicamente uma briga familiar possa custar as vidas de tantos milhes de jovens homens', diz ator Rhys Ifans sobre conflito presente no filme, que estreia esta quinta (6)."
          data="06/01/2022"
          hora="06h00"
          redator="Cesar Soto, g1"
          imgCapa={"https://s2.glbimg.com/KR7PU8oQIKqm_I4tvt8olg_dyOc=/0x0:2578x1450/924x0/smart/filters:strip_icc()/i.s3.glbimg.com/v1/AUTH_59edd422c0c84a879bd37670ae4f538a/internal_photos/bs/2022/V/i/aWlsbdQAOOqBB0o3D6qw/kings-man-origem-ralph-fiennes-2.jpg"}
          texto1={`Em dois filmes, a franquia cinematogrfica "Kingsman" explorou o mundo da espionagem britnica, com pitadas de humor e sequncias de ao que conquistaram o pblico com seus exageros  como a capanga com prteses afiadas nas pernas.

          No novo captulo que estreia nesta quinta-feira (6), "King's Man: A origem", alm de trocar de nome por algum motivo, vai at o incio do sculo 20 para explorar um outro tipo de absurdo: o da Primeira Guerra Mundial.
          
          "Nada  mais louco que a Primeira Guerra Mundial. Os eventos que levaram a ela.  incrvel que basicamente uma briga familiar possa custar as vidas de tantos milhes de jovens homens", diz o ator gals Rhys Ifans ("O Espetacular Homem-Aranha") em entrevista ao g1.`}
          video1={"https://www.youtube.com/embed/no0d18qvw0o"}
          texto2={`Aos 54 anos, ele se junta ao diretor e roteirista Matthew Vaughn e a um elenco encabeado por Ralph Fiennes ("O grande Hotel Budapeste") para interpretar uma das figuras vilanescas mais conhecidas da histria, o mstico russo Grigori Rasputin.`}

          citacao={`"H tantos elementos no filme que so malucos e loucos, mas na verdade esto corretos historicamente. Por exemplo, havia membros do Servio Secreto britnico realmente presentes, que tiveram uma participao integral no assassinato de Rasputin. Ento, no achei que nada era louco demais."`}
          texto3={`Se o subttulo no deixa claro o suficiente, "King's Man" retrata a origem da organizao secreta de espionagem no comeo do sculo 20, quando um duque (Fiennes) investe seus recursos e habilidades para impedir que viles histricos dem incio  Grande Guerra.

          Esse contexto provoca um ritmo um pouco menos descontrado que os dois filmes anteriores. Afinal, o conflito resultou em cerca de 20 milhes de mortes entre 1914 e 1918.`}

          imgFim={"https://s2.glbimg.com/CskhHsmqB5tZL_cpKg1PzjRbu_A=/0x0:2578x1080/1008x0/smart/filters:strip_icc()/i.s3.glbimg.com/v1/AUTH_59edd422c0c84a879bd37670ae4f538a/internal_photos/bs/2022/V/3/ItiowTRAqKMC5r8DmmBA/kings-man-origem-rhys-ifans.jpg"}

        />

        <BlogPage1
          titulo="Anvisa aprova registro de insumo da Fiocruz, e Brasil ter vacina 100% nacional contra Covid-19"
          subtitulo="Aprovao era a etapa final do processo de transferncia de tecnologia da vacina da AstraZeneca. A previso da Fiocruz  de que as primeiras doses do imunizante sejam envasadas ainda em janeiro e entregues ao Ministrio da Sade em fevereiro."
          data="06/01/2022"
          hora="06h00"
          redator="g1"
          videoCapa={"https://www.youtube.com/embed/wkXWAnkxAt0"}

          texto1={`A Agncia Nacional de Vigilncia Sanitria (Anvisa) aprovou, nesta sexta-feira (7), o registro do insumo farmacutico ativo (IFA) da vacina da AstraZeneca contra a Covid-19 fabricado pela Fundao Oswaldo Cruz (Fiocruz). Com isso, o Brasil ter um imunizante produzido totalmente em territrio nacional (veja mais no vdeo acima).


          Essa etapa era a ltima do processo de transferncia de tecnologia da produo da vacina. Agora, o "ingrediente" da vacina (ou seja, o IFA) ser produzido no pas, em vez de ser importado de fora. No ano passado, a Fiocruz precisou atrasar a entrega de vrios lotes de vacina por falta do IFA.
          
          A Fiocruz tem o equivalente a 21 milhes de doses em IFA nacional, em diferentes etapas de produo e controle de qualidade. A previso  a de que as primeiras doses do imunizante sejam envasadas ainda em janeiro e entregues ao Ministrio da Sade em fevereiro.`}



        />

      </div>

      <div >
        <Rodape />
      </div>
    </div>
  );
}

export default Blog;